import interfaces from '../../utils/interfaces.js';
var common = require('../../utils/common.js');
import app from "../../App.vue";
export default {
	data() {
		return {
			user_info: '',
			token: '',
			name: '',
			thumb: ''
		}
	},
	onShow() {
		this.user_info = app.globalData.user_info;
		if (this.user_info) {
			this.token = app.globalData.user_info.token;
			this.name = app.globalData.user_info.storeName;
			this.thumb = app.globalData.user_info.thumb;
		} else {
			wx.reLaunch({
				url: '/pages/index/index',
			})
		}
	},
	methods: {
		callPhone(n) {
			wx.makePhoneCall({
				phoneNumber: n
			});
		},

		tip: function() {
			wx.showToast({
				title: '功能未开放，敬请期待',
				icon: "none",
			});
		},
	}
}
